* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: white;
  font-style: arial;
}

hr {
  width: 98%;
  margin: auto;
}

a.anchor {
  display: block;
  position: relative;
  visibility: hidden;
}

.username {
  font-weight: normal;
  margin: 1em 1em 0.5em 1em;
}

.aboutme {
  margin-left: 2em;
  margin-bottom: 0.5em;
}